}

.ps__rail-x {
  height: 0.9375rem;
  bottom: 0;
}

.ps__rail-y {
  width: 0.9375rem;
  right: 0;
}

.ps--active-x > .ps__rail-x,
.ps--active-y > .ps__rail-y {
  display: block;
  background-color: transparent;
}

.ps:hover > .ps__rail-x,
.ps:hover > .ps__rail-y {
  opacity: 0.6;
}

.ps--focus > .ps__rail-x,
.ps--focus > .ps__rail-y {
  opacity: 0.6;
}

